import { createStore } from 'vuex'

export default createStore({
    state: {
        mode: 'light',
        maskActive: false
    },
    getters: {
        getMaskActive: state => {
            return state.maskActive
        }
    },
    mutations: {
        // ici synchrone
        toggleMode(state) {
            if (state.mode === 'light') {
                state.mode = 'dark'
            } else if (state.mode === 'dark') {
                state.mode = 'light'
            }
        },
        togMaskActive(state) {
            state.maskActive = !state.maskActive
        }

    },
    actions: {
        // ici asynchrone
    },
    modules: {}
})